import React, { useEffect } from "react";
import SliderItem from "./SliderItem";
import { VscChevronRight, VscChevronLeft } from "react-icons/vsc";

const Slider = () => {
  useEffect(() => {
    const sliderElems = document.querySelectorAll(".slider-item");
    const prev = document.querySelector(".prev");
    const next = document.querySelector(".next");
    // const width = window.innerWidth;
    const totalSlides = sliderElems.length;
    [...sliderElems].forEach((item, i) => {
      item.dataset.pos = i;
      item.dataset.current = i === 0 ? true : false;
    });
    prev.addEventListener("click", (e) => {
      prevSlide();
    });
    next.addEventListener("click", (e) => {
      nextSlide();
    });
    resetSlide();
    function getPrevSlideNum() {
      let current = parseInt(
        document.querySelector("[data-current='true']").dataset.pos,
        10
      );
      if (current === 0) {
        finalSlide("-4px", "-13px");
        return 0;
      }
      let prev = current - 1;
      return prev;
    }
    function getNextSlideNum() {
      let current = document.querySelector("[data-current='true']");
      let next = parseInt(current.dataset.pos, 10) + 1;
      if (next === totalSlides) {
        finalSlide("4px", "13px");
        return next - 1;
      }
      return next;
    }
    function resetSlide() {
      document.querySelector(".slider-items").style.height =
        document.querySelector(".slider-item").offsetHeight + 100 + "px";
      let current = document.querySelector("[data-current='true']");
      current.classList.add("current-item");
      let nextSlight = document.querySelector(
        "[data-pos='" + getNextSlideNum() + "']"
      );
      moveRightInSlight(nextSlight);
    }
    function nextSlide() {
      let current = document.querySelector("[data-current='true']");
      let next = document.querySelector(
        "[data-pos='" + getNextSlideNum() + "']"
      );
      current.dataset.current = false;
      next.dataset.current = true;
      moveLeft(current);
      moveRightIn(next);
      positionSlides(next);
    }
    function prevSlide() {
      let current = document.querySelector("[data-current='true']");
      let prev = document.querySelector(
        "[data-pos='" + getPrevSlideNum() + "']"
      );
      current.dataset.current = false;
      prev.dataset.current = true;
      moveRight(current);
      moveLeftIn(prev);
      positionSlides(prev);
    }
    function positionSlides(pos) {
      let prev = getPrevSlideNum();
      let next = getNextSlideNum();
      document.querySelectorAll("[data-pos]").forEach((item) => {
        item.classList.remove("left", "slight-left", "slight-right");
        if (parseInt(item.dataset.pos, 10) < parseInt(pos.dataset.pos, 10)) {
          item.classList.add("left");
        }
      });
      if (parseInt(pos.dataset.pos, 10) !== prev) {
        moveLeftInSlight(document.querySelector("[data-pos='" + prev + "']"));
      }
      if (parseInt(pos.dataset.pos, 10) !== next) {
        moveRightInSlight(document.querySelector("[data-pos='" + next + "']"));
      }
    }
    function moveLeft(elem) {
      elem.classList.remove("current-item");
    }
    function moveLeftIn(elem) {
      elem.classList.add("current-item");
    }
    function moveRight(elem) {
      elem.classList.remove("current-item");
    }
    function moveRightIn(elem) {
      elem.classList.add("current-item");
    }
    function moveRightInSlight(elem) {
      elem.classList.add("slight-right");
    }
    function moveLeftInSlight(elem) {
      elem.classList.add("slight-left");
    }
    function finalSlide(one, two) {
      let current = document.querySelector("[data-current='true']");
      var finalSlideAnim = [
        {
          offset: 0,
          right: "0px",
          position: "relative"
        },
        {
          offset: 0.5,
          right: one,
          position: "relative",
          filter: "blur(2px)"
        },
        {
          offset: 1,
          right: "0px",
          position: "relative"
        }
      ];
      var finalSlideAnim2 = [
        {
          offset: 0,
          right: "0px"
        },
        {
          offset: 0.7,
          right: two,
          filter: "blur(2px)"
        },
        {
          offset: 1,
          right: "0px"
        }
      ];
      current.querySelector(".slider-body").animate(finalSlideAnim, {
        duration: 400,
        iterations: 1,
        delay: 0
      });
      current.querySelector(".slider-pic").animate(finalSlideAnim2, {
        duration: 400,
        iterations: 1,
        delay: 0
      });
    }
  });
  return (
    <div className="Slider">
      <div className="introHead margin">Quickview</div>
      <div className="slider-items">
        <SliderItem
          head="How to add a driver's license or state ID to Apple Wallet."
          text=""
        />
        <SliderItem head="Accessibilityâ€”Developing Locally" text="" />
        <SliderItem
          head="Getting Started with Full-Stack Engineering"
          text=""
        />
        <SliderItem
          head="Bootstrap: Creating Menus, Navbars, and Modals"
          text=""
        />
        <SliderItem head="CSS Visual Rules in Chrome Inspector" text="" />
      </div>
      <div className="button-grp margin">
        <button className="prev backdrop">
          <VscChevronLeft />
        </button>
        <button className="next backdrop">
          <VscChevronRight />
        </button>
      </div>
    </div>
  );
};
export default Slider;
