import React, { Component } from "react";
import { VscChevronRight } from "react-icons/vsc";
export default class Box extends Component {
  render() {
    return (
      <div className="boxStyle backdrop">
        <div className="boxStyle-head">{this.props.text}</div>
        <div className="boxStyle-body">{this.props.text2}</div>
        <div class="inside-boxes">
          {this.props.items
            ? this.props.items.map((item, i) => {
                return (
                  <div
                    className={
                      "boxStyle-item " +
                      (this.props.items.length - 1 === i ? "last" : "")
                    }
                  >
                    {item}
                  </div>
                );
              })
            : null}
        </div>
        <div className="boxStyle-icon">
          <VscChevronRight />
        </div>
      </div>
    );
  }
}
