import "./styles.css";
import React, { Component, useEffect } from "react";
import Home from "./components/Home";
import Extra from "./components/Extra";
import NavBar from "./components/Navigation";
import { BrowserRouter, Route, Switch, useLocation } from "react-router-dom";
// require("./js.js");
const App = () => {
  // export default class App extends Component {
  const mainRef = React.createRef();
  useEffect(() => {
    //START INSERTION OBSERVER
    // define an observer instance
    var observer = new IntersectionObserver(onIntersection, {
      root: null, // default is the viewport
      threshold: 0.5 // percentage of taregt's visible area. Triggers "onIntersection"
    });
    // callback is called on intersection change
    function onIntersection(entries, opts) {
      entries.forEach((entry) => {
        console.log(entry);
        entry.target.classList.toggle("visible", entry.isIntersecting);
      });
    }
    // Use the bserver to observe an element
    // observer.observe(document.querySelector(".box"));
    //END INSERTION OBSERVER
    setTimeout(() => {
      processing();
    }, 4000);
    function processing() {
      var processing = [
        {
          offset: 0,
          background: "var(--color-one)"
        },
        {
          offset: 0.2,
          background: "var(--color-one)"
        },
        {
          offset: 0.25,
          background: "transparent",
          border: "1px solid var(--color-one)"
        },
        {
          offset: 0.5,
          background: "transparent",
          border: "1px solid transparent"
        },
        {
          offset: 0.85,
          background: "transparent",
          border: "1px solid var(--color-one)"
        },
        {
          offset: 0.9,
          background: "var(--color-one)"
        },
        {
          offset: 1,
          background: "var(--color-one)"
        }
      ];
      document.getElementById("logo1").animate(processing, {
        duration: 5000,
        iterations: Infinity,
        easing: "cubic-bezier(1, 0, 0.35, 1.08)",
        delay: 1000
      });
      document.getElementById("logo2").animate(processing, {
        duration: 5000,
        iterations: Infinity,
        easing: "cubic-bezier(1, 0, 0.35, 1.08)",
        delay: 1100
      });
      document.getElementById("logo3").animate(processing, {
        duration: 5000,
        iterations: Infinity,
        easing: "cubic-bezier(1, 0, 0.35, 1.08)",
        delay: 1200
      });

      var scaling = [
        {
          offset: 0
        },
        {
          offset: 1
        }
      ];
      document.getElementById("logospine").animate(scaling, {
        duration: 5000,
        iterations: Infinity,
        easing: "cubic-bezier(1, 0, 0.35, 1.08)",
        delay: 1200
      });
    }
    document.getElementById("color").addEventListener("input", (e) => {
      let one = e.target.value
        .match(/[A-Za-z0-9]{2}/g)
        .map((v) => parseInt(v, 16));
      document
        .querySelector(":root")
        .style.setProperty("--color-one", one.join(","));
      one[2] = one[2] - 85;
      document.querySelector(":root").style.setProperty("--text-color", one);
    });
  });

  return (
    <>
      <NavBar />
      <input type="color" id="color" />
      <div data-scroll-container data-scroll-section ref={mainRef}>
        <Switch>
          <Route path="/csb-vdo8m" component={Home} exact />
          <Route path="/extra" component={Extra} />
          <Route path="/works" component={Extra} />
          {/* <Route component={Error} /> */}
        </Switch>
      </div>
    </>
  );
};
export default App;
